  vertical-align: middle;
}
.btn-group > .btn:first-child {
  margin-left: 0;
}
.btn {
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
      touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
}
/********************************/
a {
  text-decoration: none;
}
.bg-grey {
  background: #f6f6f6;
}
.bg-white {
  background: #fff;
}
.banner {
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 0;
}
/** before after**/
.btn-toolbar:before,
.row:before {
  display: table;
  content: " ";
}
.btn-toolbar:after,
.row:after {
  display: table;
  content: " ";
  clear: both;
}
.page:after,
#main:after,
.clear:after,
.container:after {
  content: "";
  display: block;
  height: 0;
  width: 0;
  clear: both;
  zoom: 1;
}
:after,
:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

